/**
 *         
 */
export const adaptToClient = {
  /**
   *         
   * @param {Object} point -     
   * @returns {Object} -     
   */
  point: (point) => ({
    id: point.id,
    basePrice: point.base_price,
    dateFrom: point.date_from,
    dateTo: point.date_to,
    destination: point.destination,
    isFavorite: point.is_favorite,
    offers: point.offers ?? [],
    type: point.type
  }),

  /**
   *          
   * @param {Array} points -      
   * @returns {Array} -      
   */
  points: (points) => points.map(adaptToClient.point),

  /**
   *         
   * @param {Object} destination -     
   * @returns {Object} -     
   */
  destination: (destination) => destination,

  /**
   *          
   * @param {Array} destinations -      
   * @returns {Array} -      
   */
  destinations: (destinations) => destinations,

  /**
   *        
   * @param {Array} offers -    
   * @returns {Array} -    
   */
  offers: (offers) => offers,
};

/**
 *          
 */
export const adaptToServer = {
  /**
   *         
   * @param {Object} point -     
   * @returns {Object} -     
   */
  point: (point) => {
    // ,  basePrice -  
    const basePrice = typeof point.basePrice === 'string'
      ? parseInt(point.basePrice, 10)
      : point.basePrice;

    // ,  offers -  
    const offers = Array.isArray(point.offers) ? point.offers : [];

    return {
      'id': point.id,
      'base_price': Number(basePrice),
      'date_from': point.dateFrom,
      'date_to': point.dateTo,
      'destination': point.destination,
      'is_favorite': Boolean(point.isFavorite),
      'offers': offers,
      'type': point.type
    };
  },
};
